import { AES, SHA256 } from "crypto-js";
import { BXUser } from "../types/bitrix/bitrix-type";
import encUtf8 from 'crypto-js/enc-utf8';


export function getDepartmentKey() {
  const today = new Date();
  const formattedDate = `${today.getMonth() + 1}${today.getDate()}${today.getFullYear()}`;
  return `department_${formattedDate}`;
}

//      LocalStorage
export function saveDepartmentToLocalStorage(users: Array<BXUser>, key: string, domain: string) {
  const encryptedUsers = AES.encrypt(JSON.stringify(users), domain).toString(); //   
  localStorage.setItem(key, JSON.stringify({
    data: encryptedUsers,
    timestamp: new Date().toISOString() //   /
  }));

}

//       LocalStorage
export function getDepartmentFromLocalStorage(key: string, domain: string) {
  const storedData = localStorage.getItem(key);
  if (storedData) {
    const parsedData = JSON.parse(storedData);
    const storedDate = new Date(parsedData.timestamp);

    // ,     ,   
    const oneDay = 24 * 60 * 60 * 1000; //    
    if ((new Date().getTime() - storedDate.getTime()) > oneDay) {
      return null; //  
    }
    const decryptedData = AES.decrypt(parsedData.data, domain).toString(encUtf8); //  
    try {
      const decryptedData = AES.decrypt(parsedData.data, domain).toString(encUtf8); //  
      return JSON.parse(decryptedData); //    
    } catch (e) {
      console.error('Error decrypting data:', e);
      return null; //   
    }
  }
  return null; //  
}

